import { reactive, toRefs } from 'vue';

export default function() {
	let state = reactive({
		names: [],
		isLoading: false,
		isAnimated: true,
	});

	function readApi(url) {
		state.isLoading = true;

		return fetch(url)
					.then(response => response.json())
					.then(data => {
						state.names = data;
						state.isLoading = false;
						state.isAnimated = false;
					});
	}

	return {
		...toRefs(state),
		readApi,
	}
}